import React from 'react';

function isReactElementWithClassName(element: unknown): element is React.ReactElement<{ className?: string }> {
  return (
    React.isValidElement(element) &&
    typeof element === 'object' &&
    element !== null &&
    'props' in element &&
    typeof (element as any).props.className === 'string'
  );
}

export function PageHeader({
  icon: Icon,
  title,
  date,
  onDateChange,
  action,
  className = '',
  children,
}: {
  icon?: React.ElementType;
  title: React.ReactNode;
  date?: string;
  onDateChange?: (date: string) => void;
  action?: React.ReactNode;
  className?: string;
  children?: React.ReactNode;
}) {
  return (
    <div
      className={
        'sticky top-0 z-30 bg-white/30 dark:bg-black/30 backdrop-blur-md border-b border-border/40 shadow-sm flex flex-col sm:flex-row items-stretch sm:items-center justify-between gap-y-2 sm:gap-y-0 gap-x-0 sm:gap-x-4 px-2 py-2 sm:py-0 mb-2 sm:mb-0 min-h-[56px] h-auto sm:h-14 ' +
        className
      }
      aria-label="Page header"
    >
      {/* Icon + Title */}
      <div className="flex items-center gap-2 min-w-0 w-full sm:w-auto justify-center sm:justify-start">
        {Icon && <Icon className="h-6 w-6 text-primary flex-shrink-0" />}
        <h1 className="truncate text-xl sm:text-2xl md:text-3xl font-bold bg-gradient-to-r from-primary to-accent bg-clip-text text-transparent drop-shadow-md text-center sm:text-left w-full">
          {title}
        </h1>
      </div>
      {/* Date Filter */}
      {date !== undefined && onDateChange && (
        <div className="flex items-center gap-2 w-full sm:w-auto">
          <label htmlFor="page-header-date" className="text-xs sm:text-sm font-medium text-muted-foreground whitespace-nowrap">Date</label>
          <input
            id="page-header-date"
            type="date"
            value={date}
            onChange={e => onDateChange(e.target.value)}
            className="w-full sm:w-auto px-2 py-2 rounded-md border border-input bg-background text-xs sm:text-sm focus-visible:ring-2 focus-visible:ring-primary min-h-[44px]"
            aria-label="Select date"
          />
        </div>
      )}
      {/* Actions */}
      {action && (
        <div className="w-full sm:w-auto flex flex-col sm:flex-row gap-2 sm:gap-0 mt-0">
          {isReactElementWithClassName(action)
            ? React.cloneElement(action as React.ReactElement<any>, {
                className: (action.props.className || '') + ' w-full sm:w-auto min-h-[44px]',
              })
            : action}
        </div>
      )}
      {children}
    </div>
  );
} 